.work {
  padding: 60px 0;

  &__body {
    position: relative;
    max-width: 1330px;
    margin: auto;
    border-radius: 30px;
    padding: 50px;
    background-color: var(--blue-color);
  }

  &__title {
    margin-bottom: 30px;
    color: var(--white-color);
  }

  &__nav {
    gap: 15px;

    &-item {
      width: 60px;
      height: 60px;
    }

    &-btn {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 60px;
      height: 60px;
      padding: 15px;
      border-radius: 100%;
      background-color: var(--white-color);
      transition: .2s ease-in;

      &--next {
        svg {
          transform: rotate(180deg);
        }
      }

      svg {
        width: 30px;
        height: 30px;
        stroke: var(--blue-color);
        transition: .2s ease-in;
      }

      &:focus {
        outline: none;
      }

      @include hover {
        background-color: var(--pink-color);

        svg {
          stroke: var(--white-color);
        }
      }

      &:active {
        background-color: var(--grey-color);

        svg {
          stroke: var(--white-color);
        }
      }
    }
  }

  &__slider {
    margin-bottom: 60px;
    overflow: hidden;

    &-bottom {
      position: relative;
      display: flex;
      align-items: center;
    }
  }

  &__btn {
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
  }

  &__slide {
    img {
      display: block;
      border-radius: 20px;
      width: 100%;
      height: 290px;
      object-fit: cover;
    }
  }
}

.work__body-star {
  position: absolute;
  right: 0;
  bottom: 0;
  z-index: 1;
  width: 190px;
  height: 150px;
  animation: twinkle 2s infinite;
  animation-timing-function: cubic-bezier(0.5, 0, 0.5, 1);
  fill: var(--white-color);
  opacity: 0.2;
}

.tabs {
  position: relative;
  z-index: 5;
  overflow: scroll;
  display: flex;
  justify-content: space-between;
  gap: 10px;
  margin-bottom: 60px;
}

.tab {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border: 1px solid var(--light-grey-color);
  border-radius: 100px;
  min-width: 154px;
  padding: 10px 30px;
  font-size: clamp(1rem, 0.964rem + 0.18vw, 1.125rem);
  font-weight: var(--semibold-font);
  line-height: 144%;
  color: var(--light-grey-color);
  text-align: center;
  transition: 0.2s ease-in;

  &:focus {
    outline: none;
  }

  @include hover {
    color: var(--blue-color);
    background-color: var(--light-grey-color);
  }

  &:active {
    border-color: var(--pink-color);
    color: var(--white-color);
    background-color: var(--pink-color);
  }

  &.active {
    color: var(--blue-color);
    background-color: var(--light-grey-color);
  }
}

.tab-content {
  position: relative;
  z-index: 2;
  display: none;
  transition: opacity 0.3s ease;

  &[data-tab].active {
    display: block;
    opacity: 1;
  }
}


@media screen and (max-width:1024px) {
  .work {
    &__body {
      border-radius: 0;
    }
  }
}

@media screen and (max-width:767px) {
  .work {
    &__body {
      padding: 50px 15px;
    }
    &__slider {
      &-bottom {
        flex-direction: column;
        align-content: center;
        gap: 15px;
      }
    }
    &__btn {
      position: relative;
    }
  }
  .tabs {
    margin-bottom: 30px;
  }
  .tab {
    padding: 5px 10px;
    &-content {
      padding: 0;
    }
  }
}
