html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
p,
ul,
ol {
  margin: 0;
  padding: 0;
}

.page {
  overflow-x: hidden;
  height: 100%;
}

.page__body {
  position: relative;
  overflow-x: hidden;
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-family: var(--font-family);
  font-weight: var(--regular-font);
  font-size: 16px;
}

img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  width: 100%;
  min-height: 100vh;
}

.main {
  position: relative;
  flex: 1 1 auto;
}

.is-hidden {
  display: none !important;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.flex {
  display: flex;
}

.grid {
  display: grid;
}

.centered {
  text-align: center;
}

.page--ios .dis-scroll {
  position: relative;
}

.primary__btn {
  position: relative;
  z-index: 2;
  overflow: hidden;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  width: 220px;
  padding: 20px 25px;
  background-color: var(--pink-color);
  transition: .2s ease-in;

  span {
    position: relative;
    z-index: 2;
    font-size: clamp(1rem, 0.964rem + 0.18vw, 1.125rem);
    font-weight: var(--semibold-font);
    line-height: 133%;
    color: var(--white-color);
    transition: color .2s ease-in;
  }

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    background-color: var(--white-color);
    transform: translateX(-100%);
    transition: all .2s ease-in;
  }

  &:focus {
    outline: none;
  }

  @include hover {
    span {
      color: var(--blue-color);

    }

    &::before {
      transform: translateX(0);
    }
  }

  &:active {
    span {
      color: var(--white-color);

    }

    &::before {
      background-color: var(--blue-color);
    }
  }

  &--white {
    background-color: var(--white-color);

    span {
      color: var(--pink-color);
    }

    &:focus {
      outline: none;
    }

    @include hover {
      span {
        color: var(--white-color);

      }

      &::before {
        background-color: var(--blue-color);
        transform: translateX(0);
      }
    }

    &:active {
      span {
        color: var(--white-color);

      }

      &::before {
        background-color: var(--pink-color);
      }
    }
  }
}

.section {
  padding: 60px 0;

  &__title {
    margin-bottom: 60px;
    font-size: clamp(1.5rem, 1.107rem + 1.96vw, 2.875rem);
    font-weight: var(--semibold-font);
    color: var(--black-color);
    line-height: 110%;
  }

  &__text {
    font-size: clamp(1rem, 0.964rem + 0.18vw, 1.125rem);
    line-height: 144%;
    color: var(--black-color);

    &:not(:last-child) {
      margin-bottom: 15px;
    }
  }
}

.breadcrumbs {
  align-items: center;
  flex-wrap: wrap;
  gap: 15px;
  margin-bottom: 30px;

  &__item {
    position: relative;

    &:not(:last-child) {
      padding-right: 30px;

      &::after {
        content: "";
        position: absolute;
        right: 5px;
        top: 50%;
        width: 5px;
        height: 5px;
        border-radius: 1px;
        background-color: var(--blue-color);
        opacity: .5;
        transform: translateY(-50%);
      }
    }
  }

  &__link {
    position: relative;
    display: flex;
    align-items: center;
    overflow: hidden;

    span {
      font-size: 14px;
      line-height: 150%;
      color: var(--black-color);
      transition: color .2s ease-in-out;
    }

    svg {
      margin-right: 5px;
      width: 20px;
      height: 20px;
      fill: var(--blue-color);
      transition: fill .2s ease-in-out;
    }


    &:focus {
      outline: none;
    }

    @include hover {
      span {
        color: var(--pink-color);
      }

      svg {
        fill: var(--pink-color);
      }

    }

    &:active {
      span {
        color: var(--blue-color);
      }

    }

    &--disable {
      pointer-events: none;
      cursor: default;

      span {
        color: var(--grey-color);
        opacity: .5;
      }
    }
  }
}

.success {
  padding: 120px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 80vh;
  gap: 30px;
  &__headline {
    margin-bottom: 60px;
    font-size: clamp(1.5rem, 1.107rem + 1.96vw, 2.875rem);
    font-weight: var(--semibold-font);
    color: var(--black-color);
    line-height: 110%;
    text-align: center;
    text-transform: capitalize;
  }

  &__text {
    margin-bottom: 50px;
    font-size: clamp(1rem, 0.964rem + 0.18vw, 1.125rem);
    line-height: 144%;
    color: var(--black-color);
    text-align: center;
  }

  &__link {
    display: flex;
    margin: auto;
    width: 260px;
    text-transform: capitalize;

    @include hover {
      span {
        color: var(--white-color);

      }

      &::before {
        transform: translateX(0);
        background-color: var(--blue-color);
      }
    }

    &:active {
      &::before {
        background-color: var(--grey-color);
      }
    }
  }
}

.invalid-text {
  margin-top: 5px;
  color: red;
  font-size: 12px;
}

.primary__btn.focus-visible,
.breadcrumbs__link.focus-visible {
  outline: 1px solid rgb(246, 3, 3);
  outline-offset: 3px;
}


@media (max-width: 767px) {
  [data-aos] {
    opacity: 1 !important;
    transform: none !important;
    transition: none !important;
  }
}


@media screen and (max-width:500px) {
  .primary__btn {
    width: 100%;
  }
}
