.primary__btn {
  position: relative;
  z-index: 2;
  overflow: hidden;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  width: 220px;
  padding: 20px 25px;
  background-color: var(--pink-color);
  transition: 0.2s ease-in;
}
.primary__btn span {
  position: relative;
  z-index: 2;
  font-size: clamp(1rem, 0.964rem + 0.18vw, 1.125rem);
  font-weight: var(--semibold-font);
  line-height: 133%;
  color: var(--white-color);
  transition: color 0.2s ease-in;
}
.primary__btn::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: var(--white-color);
  transform: translateX(-100%);
  transition: all 0.2s ease-in;
}
.primary__btn:focus {
  outline: none;
}
@media (any-hover: hover) {
  .primary__btn:hover span {
    color: var(--blue-color);
  }
  .primary__btn:hover::before {
    transform: translateX(0);
  }
}
.primary__btn:active span {
  color: var(--white-color);
}
.primary__btn:active::before {
  background-color: var(--blue-color);
}
.primary__btn--white {
  background-color: var(--white-color);
}
.primary__btn--white span {
  color: var(--pink-color);
}
.primary__btn--white:focus {
  outline: none;
}
@media (any-hover: hover) {
  .primary__btn--white:hover span {
    color: var(--white-color);
  }
  .primary__btn--white:hover::before {
    background-color: var(--blue-color);
    transform: translateX(0);
  }
}
.primary__btn--white:active span {
  color: var(--white-color);
}
.primary__btn--white:active::before {
  background-color: var(--pink-color);
}

.section {
