#loader {
  position: relative;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  animation: rotate 2s linear infinite;
}

.loader-inner {
  position: absolute;
  border: 5px solid transparent;
  border-top-color: var(--pink-color);
  border-radius: 100%;
  width: 100%;
  height: 100%;
  animation: spin 2s linear infinite;
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
