.burger {
  position: relative;
  z-index: 1000;
  display: none;
  padding: 0;
  width: 40px;
  height: 40px;
  border-radius: 5px;
  padding: 10px;
  background-color: var(--blue-color);
  cursor: pointer;
}
.burger__line {
  position: absolute;
  right: 10px;
  top: 50%;
  width: 15px;
  height: 2px;
  border-radius: 10px;
  background-color: var(--white-color);
  transform: translateY(-50%);
  transition: opacity 0.2s ease-in-out;
}
.burger::before {
  content: "";
  position: absolute;
  right: 10px;
  top: 12px;
  border-radius: 10px;
  width: 18px;
  height: 2px;
  background-color: var(--white-color);
  transition: transform 0.2s ease-in-out, top 0.2s ease-in-out;
}
.burger::after {
  content: "";
  position: absolute;
  right: 10px;
  bottom: 12px;
  border-radius: 10px;
  width: 10px;
  height: 2px;
  background-color: var(--white-color);
  transition: transform 0.2s ease-in-out, bottom 0.2s ease-in-out;
}
.burger--active .burger__line {
  opacity: 0;
}
.burger--active::before {
  top: 50%;
  width: 20px;
  background-color: #fff;
  transform: rotate(45deg);
}
.burger--active::after {
  top: 50%;
  bottom: auto;
  width: 20px;
  background-color: #fff;
  transform: rotate(-45deg);
}

