  align-items: center;
  justify-content: center;
  gap: 100px;
  transform: scale(0);
  transition: 0.2s ease-in;
}
.about__info {
  max-width: 500px;
  transform: scale(0);
  transition: 0.2s ease-in;
}
.about__subtitle {
  gap: 20px;
  margin-bottom: 20px;
  font-size: clamp(1.25rem, 1.071rem + 0.89vw, 1.875rem);
  font-weight: var(--semibold-font);
  line-height: 114%;
  color: var(--pink-color);
}
.about__subtitle span {
  color: var(--black-color);
}
.about__text {
  line-height: 150%;
  color: var(--grey-color);
}
.about__images {
  position: relative;
  width: 544px;
  height: 620px;
  transform: scale(0);
  transition: 1s ease-in-out;
}
.about__image {
  position: absolute;
  left: 0;
  top: 0;
  border: 5px solid #FFF3F8;
  border-radius: 30px;
  width: 100%;
  height: 600px;
  object-fit: cover;
  transition: 0.2s ease-in;
}
.about__image--back {
  z-index: -1;
  top: -20px;
  left: -5px;
  transform: rotate(3.557deg);
}
.about__nav-btn {
  position: absolute;
  top: 50%;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  width: 60px;
  height: 60px;
  transform: translateY(-50%);
  background-color: var(--white-color);
  transition: 0.2s ease-in;
}
.about__nav-btn svg {
  width: 30px;
  height: 30px;
  stroke: var(--black-color);
  transition: 0.2s ease-in;
}
.about__nav-btn--prev {
  left: -30px;
}
.about__nav-btn--next {
  right: -30px;
}
.about__nav-btn--next svg {
  transform: rotate(180deg);
}
.about__nav-btn:focus {
  outline: none;
}
@media (any-hover: hover) {
  .about__nav-btn:hover {
    background-color: var(--pink-color);
  }
  .about__nav-btn:hover svg {
    stroke: var(--white-color);
  }
}
.about__nav-btn:active {
  background-color: var(--blue-color);
}
.about__nav-btn:active svg {
  stroke: var(--white-color);
}

@media screen and (max-width: 1024px) {
  .about__slider {
    padding: 40px 0 0 0;
  }
  .about__slide {
    flex-direction: column;
    gap: 50px;
  }
  .about__info {
    max-width: 100%;
  }
}
@media screen and (max-width: 600px) {
  .about__images {
    width: 90%;
    height: 500px;
  }
  .about__image {
    height: 500px;
  }
}
@media screen and (max-width: 600px) {
  .about__images {
    height: 400px;
  }
  .about__image {
    height: 400px;
  }
}
.services-page__title {
  margin-bottom: 50px;
  text-align: center;
}
.services-page__list {
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
}

@media screen and (max-width: 1024px) {
  .services-page__list {
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
  }
}
@media screen and (max-width: 767px) {
  .services-page__list {
    grid-template-columns: 1fr;
    gap: 15px;
  }
}