.services-page {
  &__title {
    margin-bottom: 50px;
    text-align: center;
  }
  &__list {
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
  }
}

@media screen and (max-width:1024px) {
  .services-page {
    &__list {
      grid-template-columns: repeat(2, 1fr);
      gap: 30px;
    }
  }
}

@media screen and (max-width:767px) {
  .services-page {
    &__list {
      grid-template-columns:1fr;
      gap: 15px;
    }
  }
}
