.footer {
  &__container {
    border-top: 1px solid #E2E2E2;
    padding: 30px 15px;
    align-items: center;
    justify-content: space-between;
    gap: 30px;
  }

  &__logo {
    width: 80px;
    height: 80px;
    img {
      display: block;
      width: 100%;
      height: 100%;
    }
  }

  &__list {
    gap: 20px;
    margin-bottom: 10px;
  }

  &__link {
    position: relative;
    display: block;
    overflow: hidden;
    font-weight: var(--semibold-font);
    line-height: 150%;
    color: var(--black-color);
    transition: .2s ease-in-out;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      border-radius: 10px;
      width: 100%;
      height: 1px;
      background-color: var(--pink-color);
      transform: translateX(-100%);
      transition: transform .2s ease-in-out;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--pink-color);

      &::before {
        transform: translateX(0);
      }
    }

    &:active {
      color: var(--blue-color);

      &::before {
        transform: translateX(-100%);
      }
    }
  }

  &__copyright {
    font-size: 12px;
    font-weight: 400;
    line-height: 150%;
    text-align: right;
    color: var(--grey-color);

    a {
      color: var(--grey-color);
      transition: .2s ease-in;

      &:focus {
        outline: none;
      }

      @include hover {
        color: var(--pink-color);
      }

      &:active {
        color: var(--blue-color);
      }
    }
  }
}

@media screen and (max-width:600px) {
  .footer {
    &__container {
      flex-direction: column;
      align-items: center;
    }
    &__col {
      width: 100%;
    }
    &__list {
      display: grid;
      grid-template-columns: 1fr 1fr;
      width: 100%;
    }
    &__copyright {
      text-align: center;
    }
  }
}
