.services {
  &__grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 30px;
  }

  &__item {
    position: relative;
    z-index: 2;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    border-radius: 30px;
    min-height: 517px;
    overflow: hidden;
    background-size: cover;
    background-position: 50% 50%;
    background-repeat: no-repeat;

    &-info {
      position: relative;
      z-index: 3;
      padding: 30px;
      width: 100%;
      transform: translateY(20%);
      transition: .2s ease-in-out;

    }

    &-title {
      margin-bottom: 30px;
      font-size: clamp(1.25rem, 0.982rem + 1.34vw, 2.188rem);
      font-weight: var(--semibold-font);
      color: var(--white-color);
      text-align: center;
      line-height: 114%;
    }

    &-link {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 10px;
      overflow: hidden;
      width: 200px;
      margin: auto;
      padding: 20px 25px;
      border-radius: 100px;
      text-align: center;
      background-color: var(--blue-color);
      transform: translateY(200%);
      transition: .2s ease-in-out;

      span {
        position: absolute;
        left: 50%;
        top: 50%;
        width: 100%;
        font-size: clamp(1rem, 0.964rem + 0.18vw, 1.125rem);
        font-weight: var(--semibold-font);
        line-height: 133%;
        color: var(--white-color);
        transform: translate(-50%, -50%);
        transition: .2s ease-in;
      }

      svg {
        position: relative;
        left: 70px;
        width: 24px;
        height: 24px;
        stroke: var(--white-color);
        opacity: 0;
        transition: opacity .2s ease-in;
      }

      &:focus {
        outline: none;
      }

      @include hover {
        span {
          transform: translate(-60%, -50%);
        }

        svg {
          opacity: 1;
        }
      }

      &:active {
        background-color: var(--pink-color);
      }
    }

    @include hover {
      .services__item-link {
        transform: translateY(0);
      }

      .services__item-info {
        transform: translateY(0);
      }
    }

  }

  &__title {
    margin-bottom: 0;
  }
  &__row {
    margin-bottom: 60px;
    align-items: center;
    justify-content: space-between;
    gap: 30px;
  }

  &__slider {
    margin-bottom: 60px;
  }

  &__link {
    display: flex;
    margin: auto;

    @include hover {
      span {
        color: var(--white-color);

      }

      &::before {
        transform: translateX(0);
        background-color: var(--blue-color);
      }
    }

    &:active {
      &::before {
        background-color: var(--grey-color);
      }
    }
  }
}

.services__nav {
  display: none;
}

.services__nav-btn--next {
  transform: rotate(180deg);
}

.wave {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  height: 232px;
}

#wave {
  width: 100%;
  animation: wave 5s infinite;
}

@keyframes wave {
  0% {
    d: path('M0.666626 233V21C17.253 18.1667 63.293 6.48014 120.586 17.5C227.167 38 207.664 46 279.317 46C336.64 46 392.768 15.3333 413.667 0V233H0.666626Z');
  }

  50% {
    d: path('M0.666626 233V21C17.253 26.1667 63.293 46.4801 120.586 57.5C227.167 68 207.664 46 279.317 36C336.64 26 392.768 15.3333 413.667 0V233H0.666626Z');
  }

  100% {
    d: path('M0.666626 233V21C17.253 18.1667 63.293 6.48014 120.586 17.5C227.167 38 207.664 46 279.317 46C336.64 46 392.768 15.3333 413.667 0V233H0.666626Z');
  }
}

@media screen and (max-width:1024px) {
  .services {
    &__grid {
      grid-template-columns: 1fr 1fr;
    }
    &__item {
      &-info {
        padding: 10px;
      }
    }
  }
}

@media (max-width: 600px) {
  .services__slider .swiper-wrapper {
    display: flex;
    column-gap: 0;
  }

  .services__nav {
    display: flex;
  }

  .services {
    &__item {
      &-info {
        padding: 10px 10px 20px 10px;
        transform: translateY(0);
      }
      &-link {
        transform: translateY(0);
      }
    }
  }
}
