.success__modal {
  display: none;
  /* Hidden by default */
  position: fixed;
  z-index: 100000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);

  &-title {
    margin-bottom: 15px;
    font-size: 36px;
    font-weight: var(--semibold-font);
  }

  &-text {
    margin-bottom: 50px;
    color: var(--grey-color);
    text-align: center;
    font-size: 25px;
    font-style: normal;
    line-height: 140%;
  }
}

.success__modal-content {
  position: absolute;
  left: 50%;
  top: 50%;
  padding: 70px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 727px;
  max-width: 90%;
  min-height: 476px;
  border-radius: 30px;
  background-color: var(--white-color);
  transform: translate(-50%, -50%);
}

.close {
  position: absolute;
  right: 20px;
  top: 20px;
  font-size: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  color: var(--blue-color);
  transition: .2s ease-in-out;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

@media screen and (max-width:768px) {
  .success__modal {
    &-content {
      padding: 70px 30px;
    }
  }
}

@media screen and (max-width:500px) {
  .success__modal {
    &-content {
      min-height: 300px;
    }

    &-text {
      font-size: 18px;
    }
  }
}
