.service {
  padding: 200px 0 60px;

  &__title {
    margin-bottom: 30px;
  }

  &__bg {
    margin-bottom: 30px;
    height: 450px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-attachment: fixed;

    &--seasonal {
      background-image: url("../img/services/seasonal-cleaning.jpg");
    }
  }

  &__image {
    width: 50%;
    height: 400px;
    max-width: 100%;

    &--right {
      float: right;
      margin: 15px 0 15px 15px;
    }

    &--left {
      float: left;
      margin: 15px 15px 15px 0;
    }

    img {
      border-radius: 30px;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}


.faq {
  padding-bottom: 60px;

  &__title {
    margin-bottom: 30px;
    text-align: center;
  }
}


.ac {
  margin-top: 0;
  border: none;
  background-color: transparent;
}

.accordion {
  &__list {
    margin: auto;
    width: 60vw;
    max-width: 100%;
  }

  &__item {
    border-bottom: 1px solid var(--grey-color);
    padding: 20px 0;

    &:first-child {
      border-top: 1px solid var(--grey-color);
    }
  }

  &__top {
    align-items: center;
    justify-content: space-between;
    gap: 10px;
    margin-bottom: 15px;

    &--left {
      gap: 30px;
    }
  }

  &__bottom {
    margin-left: 80px;
    max-width: 789px;
  }

  &__text {
    font-size: clamp(0.875rem, 0.839rem + 0.18vw, 1rem);
    line-height: 136%;
    color: var(--grey-color);
  }

  &__number {
    font-size: clamp(1rem, 0.929rem + 0.36vw, 1.25rem);
    font-weight: var(--semibold-font);
    line-height: 120%;
    color: var(--blue-color);
  }

  &__subtitle {
    max-width: 770px;
    font-size: clamp(1.125rem, 1.054rem + 0.36vw, 1.375rem);
    font-weight: var(--semibold-font);
    line-height: 120%;
    color: var(--color-white);
    letter-spacing: -0.28px;
  }

  &__btn {
    cursor: pointer;
    position: relative;
    display: block;
    width: 43px;
    height: 43px;
    border: 1px solid var(--grey-color);
    border-radius: 50%;

    &::before {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      width: 20px;
      height: 2px;
      background-color: var(--grey-color);
      transform: translate(-50%, -50%);
    }

    &::after {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      width: 2px;
      height: 20px;
      background-color: var(--grey-color);
      transform: translate(-50%, -50%);
      transition: .2s ease-in-out;
    }
  }
}


.accordion__item.is-active .accordion__btn::after {
  opacity: 0;
}

@supports (-webkit-touch-callout: none) {
  .service__bg {
    background-attachment: scroll;
  }
}

@supports not (-webkit-touch-callout: none) {
  .service__bg {
    background-attachment: fixed;
  }
}


@media screen and (max-width:1024px) {
  .accordion {
    &__list {
      width: 100vw;
    }
  }
}

@media screen and (max-width:767px) {
  .service {
    padding: 150px 0 60px;
    &__image {

      &--right,
      &--left {
        float: none;
        margin: 15px 0;
        width: 100%;
      }
    }
  }

  .accordion {
    &__bottom {
      margin-left: 50px;
    }
  }
}
