.contact {
  padding: 60px 0 77px;

  &__body {
    position: relative;
    max-width: 1330px;
    margin: auto;
    border-radius: 30px;
    padding: 50px;
    background-color: var(--blue-color);
  }

  &__title {
    margin-bottom: 30px;
    color: var(--white-color);
  }

  &__subtitle {
    margin-bottom: 30px;
    font-size: clamp(1.25rem, 1.161rem + 0.45vw, 1.563rem);
    font-weight: var(--semibold-font);
    line-height: 140%;
    text-align: center;
    color: var(--black-color);
  }

  &__address {
    margin-bottom: 60px;
    font-style: normal;
  }

  &__list {
    align-items: center;
    justify-content: flex-start;
    gap: 50px;
  }

  &__link {
    align-items: center;
    gap: 15px;
    max-width: 332px;

    &-icon {
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 100%;
      width: 70px;
      height: 70px;
      padding: 20px;
      background-color: var(--white-color);
      transition: .2s ease-in;

      svg {
        fill: var(--blue-color);
      }
    }

    span {
      font-size: clamp(1rem, 0.964rem + 0.18vw, 1.125rem);
      font-weight: 400;
      line-height: 144%;
      color: var(--white-color);
      transition: .2s ease-in;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      span {
        color: var(--pink-color);
      }

      .contact__link-icon {
        background-color: var(--pink-color);

        svg {
          fill: var(--white-color);
        }
      }
    }

    &:active {
      span {
        color: var(--grey-color);
      }

      .contact__link-icon {
        background-color: var(--white-color);

        svg {
          fill: var(--blue-color);
        }
      }
    }

    &--nohover {
      pointer-events: none;
      cursor: default;
    }
  }

  &__image {
    position: relative;
    left: 50px;
    bottom: -21px;
    z-index: 4;
    transform: scale(1.1);
  }
  &__row {
    align-items: flex-end;
  }

  &__star {
    &--first {
      position: absolute;
      right: -50px;
      top: 0;
      fill: var(--white-color);
      opacity: 0.2;
      animation: twinkle 2s infinite;
      animation-timing-function: cubic-bezier(0.5, 0, 0.5, 1);
    }
    &--second {
      position: absolute;
      left: -50px;
      bottom: 50px;
      fill: var(--white-color);
      opacity: 0.2;
      animation: twinkle 2s infinite;
      animation-timing-function: cubic-bezier(0.5, 0, 0.5, 1);
    }
  }
}

.form__body {
  position: relative;
  z-index: 1;
  border: 1px solid #D8EDFC;
  border-radius: 30px;
  padding: 50px;
  background-color: var(--white-color);
}


.contact-form {
  .form__input,
  .custom-select {
    width: 242px;
    min-width: 100%;
  }
}

.contact-form__nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}


@media screen and (max-width:1100px)  {
  .contact {
    &__list {
      display: grid;
      grid-template-columns: 1fr 1fr;
    }
    &__image {
      display: none;
    }
    .form {
      &__body {
        width: 100%;
      }
      &__inputs {
        max-width: 100%;
      }
    }

  }
}

@media screen and (max-width:1024px) {
  .contact {
    &__body {
      border-radius: 0;
    }
  }
}

@media screen and (max-width:767px) {
  .contact {
   &__body {
    padding: 50px 15px;
   }
    &__list {
      grid-template-columns: 1fr;
      gap: 15px;
    }
    &__link {
      &-icon {
        width: 50px;
        height: 50px;
        padding: 10px;
      }
    }
    .form {
      &__body {
        padding: 50px 15px;
      }
      &__list {
        display: flex;
        flex-direction: column;
      }
    }
  }
}
