.hero {
  position: relative;
  padding: 229px 0 268px;
  background-image: url("../img/hero/hero.png");
  background-position: 50% 50%;
  background-size: cover;
  background-repeat: no-repeat;

  &--services {
    padding: 250px 0 100px;
    background-image: url("../img/hero/hero-image.jpg");
  }

  &__row {
    position: relative;
    z-index: 2;
    align-items: center;
    justify-content: space-between;
    gap: 30px;
  }

  &__info {
    width: 575px;
    max-width: 100%;
  }

  &__title {
    margin-bottom: 30px;
    font-size: clamp(2.25rem, 1.911rem + 1.7vw, 3.438rem);
    font-weight: var(--semibold-font);
    line-height: 120%;
    color: var(--white-color);
  }

  &__subtitle {
    display: inline-flex;
    justify-content: center;
    border: 1px solid var(--white-color);
    border-radius: 100px;
    margin-bottom: 30px;
    width: 402px;
    max-width: 100%;
    padding: 10px;
    font-size: clamp(0.875rem, 0.804rem + 0.36vw, 1.125rem);
    font-weight: var(--semibold-font);
    line-height: 144%;
    color: var(--white-color);
    backdrop-filter: blur(5px);
  }

  &__text {
    margin-bottom: 30px;
    font-size: clamp(1rem, 0.964rem + 0.18vw, 1.125rem);
    line-height: 144%;
    color: var(--white-color);
  }

  &__list {
    gap: 15px;
  }

  &__address {
    font-style: normal;
  }

  &__btn {
    min-width: 220px;
  }

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, rgba(0, 0, 0, 0.50) 0%, rgba(0, 0, 0, 0.00) 100%);
  }
}

.hero__social {
  display: flex;
  flex-direction: column;
  gap: 15px;

  &-link {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid rgba(255, 255, 255, 0.20);
    border-radius: 100%;
    width: 70px;
    height: 70px;
    padding: 15px;
    background-color: rgba(109, 105, 105, 0.30);
    backdrop-filter: blur(5px);
    transition: all .2s ease-in;

    img {
      display: block;
      width: 40px;
      height: 40px;
      object-fit: cover;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      border-color: var(--pink-color);
    }

    &:active {
      border-color: rgba(255, 255, 255, 0.20);
      background-color: var(--white-color);
    }
  }
}

.hero__btn.focus-visible,
.hero__social-link.focus-visible {
  outline: 1px solid rgb(246, 3, 3);
  outline-offset: 3px;
}

@media screen and (max-width:600px) {
  .hero {
    &__row {
      flex-direction: column;
    }

    &__list {
      &-item {
        width: 50%;
      }
    }

    &__btn {
      width: 100%;
    }

    &__social {
      flex-direction: row;
      justify-content: center;
    }
  }
}

@media screen and (max-width:500px) {
  .hero {
    padding: 130px 0 268px;
    &__list {
      flex-wrap: wrap;

      &-item {
        width: 100%;
      }
    }

    &__title,
    &__text {
      margin-bottom: 15px;
      text-align: center;
    }

    &__subtitle {
      justify-content: center;
      margin-bottom: 15px;
      width: 100%;
      text-align: center;
    }
  }
}
