.benefits {
  position: relative;
  padding: 60px 0;

  &__container {
    align-items: center;
    gap: 50px;
  }

  &__image {
    position: relative;
    z-index: 3;
  }

  &__info {
    max-width: 585px;
  }

  &__title {
    margin-bottom: 30px;
  }

  &__subtitle {
    margin-bottom: 15px;
  }

  &__text {
    margin-bottom: 15px;
    font-size: clamp(1rem, 0.964rem + 0.18vw, 1.125rem);
    line-height: 144%;
    color: var(--grey-color);
  }

  &__btn {
    @include hover {
      span {
        color: var(--white-color);

      }

      &::before {
        transform: translateX(0);
        background-color: var(--blue-color);
      }
    }

    &:active {
      &::before {
        background-color: var(--grey-color);
      }
    }
  }
}

.benefits-star-1 {
  position: absolute;
  right: 0;
  top: 100px;
  z-index: 1;
  width: 90px;
  height: 90px;
  fill: #34A6F8;
  animation: twinkle 2s infinite;
  animation-timing-function: cubic-bezier(0.5, 0, 0.5, 1);
}

.benefits-star-2 {
  position: absolute;
  right: 0;
  bottom: 100px;
  z-index: 1;
  width: 30px;
  height: 30px;
  fill: #34A6F8;
  animation: twinkle 2s infinite;
  animation-timing-function: cubic-bezier(0.5, 0, 0.5, 1);
}

.benefits-star-3 {
  position: absolute;
  left: 30px;
  top: 65%;
  z-index: 1;
  width: 17px;
  height: 17px;
  fill: #34A6F8;
  animation: twinkle 2s infinite;
  animation-timing-function: cubic-bezier(0.5, 0, 0.5, 1);
}

.benefits-star-4 {
  position: absolute;
  left: -15px;
  top: 45%;
  z-index: 1;
  width: 30px;
  height: 30px;
  fill: #34A6F8;
  animation: twinkle 2s infinite;
  animation-timing-function: cubic-bezier(0.5, 0, 0.5, 1);
}

@keyframes twinkle {

  0%,
  100% {
    transform: scale(.8);
    opacity: 0;
  }

  50% {
    transform: scale(1);
    opacity: 1;
  }
}

@media screen and (max-width:1024px) {
  .benefits {
    &__container {
      flex-direction: column;
    }

    &__info {
      max-width: 100%;
    }
  }
}

