.reviews {
  &__slider {
    margin-bottom: 30px;

    &::before {
      content: "";
      position: absolute;
      right: 0;
      left: 0;
      z-index: 10;
      width: 225px;
      height: 403px;
      background: linear-gradient(90deg, #FFF 0%, rgba(255, 255, 255, 0.00) 100%);
    }

    &::after {
      content: "";
      position: absolute;
      right: 0;
      top: 0;
      z-index: 10;
      width: 225px;
      height: 403px;
      background: linear-gradient(90deg, #FFF 0%, rgba(255, 255, 255, 0.00) 100%);
      transform: rotate(180deg);
    }
  }

  &__title {
    margin-bottom: 0;
  }

  &__container {
    margin-bottom: 60px;
    align-items: center;
    justify-content: space-between;
    gap: 30px;
  }

  &__nav {
    gap: 15px;

    &-btn {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 60px;
      height: 60px;
      padding: 15px;
      border-radius: 100px;
      border: 1px solid var(--grey-color);
      background: var(--white-color);

      &--next {
        transform: rotate(180deg);
      }

      svg {
        width: 30px;
        height: 30px;
        stroke: var(--grey-color);
      }
    }
  }
}

.reviews__item {
  border: 1px solid #D8EDFC;
  border-radius: 10px;
  min-height: 240px;
  padding: 30px;
  transition: .2s ease-in;

  &-top {
    margin-bottom: 20px;
    align-items: center;
    justify-content: space-between;
    gap: 10px;
  }

  &-bottom {
    align-items: center;
    justify-content: space-between;
    gap: 10px;
  }

  &-date {
    font-size: 14px;
    font-weight: var(--semibold-font);
    line-height: 140%;
    color: var(--grey-color);
    transition: .2s ease-in;
  }

  &-text {
    margin-bottom: 20px;
    font-size: 14px;
    font-weight: var(--semibold-font);
    line-height: 140%;
    color: var(--black-color);
    transition: .2s ease-in;
  }

  &-image {
    width: 95px;
  }

  &-name {
    font-size: 18px;
    line-height: 144%;
    color: var(--grey-color);
    transition: .2s ease-in;
  }

  &:focus {
    outline: none;
  }

  @include hover {
    background-color: var(--blue-color);

    .reviews__item-date,
    .reviews__item-name {
      color: var(--light-grey-color);
    }

    .reviews__item-text {
      color: var(--white-color);
    }
  }

}

.star__list {
  gap: 10px;

  &-item {
    width: 20px;
    height: 20px;

    svg {
      display: block;
      width: 100%;
      height: 100%;
      fill: var(--pink-color);
    }
  }
}

.custom-progressbar {
  width: 100% !important;
  height: 1px !important;
  background-color: #D8EDFC !important;
  position: relative;
}

.custom-progress {
  height: 100%;
  border-radius: 1px !important;
  background-color: #34A6F8 !important;
}


@media screen and (max-width:1024px) {
  .reviews {
    &__slider {
      &::before,
      &::after {
        display: none;
      }
    }
    &__item {
      &-date {
        font-size: 12px;
      }
    }
  }
  .star__list {
    gap: 5px;
  }
}
