.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  padding: 20px 0;
  width: 100%;
  background-color: var(--white-color);
  backdrop-filter: blur(5px);
  opacity: 1;
  transition: .2s ease-in;

  &__body {
    align-items: center;
    justify-content: space-between;
    gap: 5px;
  }

  &__logo {
    width: 80px;
    height: 80px;

    img {
      display: block;
      width: 100%;
      height: 100%;
    }
  }

  &__menu {
    align-items: center;
    gap: 30px;
  }

  &__list {
    align-items: center;
    gap: 20px;
  }

  &__link {
    position: relative;
    display: block;
    overflow: hidden;
    font-weight: var(--semibold-font);
    line-height: 150%;
    color: var(--black-color);
    transition: .2s ease-in-out;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      border-radius: 10px;
      width: 100%;
      height: 1px;
      background-color: var(--pink-color);
      transform: translateX(-100%);
      transition: transform .2s ease-in-out;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--pink-color);

      &::before {
        transform: translateX(0);
      }
    }

    &:active {
      color: var(--blue-color);

      &::before {
        transform: translateX(-100%);
      }
    }
  }

  &__btn {
    &:focus {
      outline: none;
    }

    @include hover {
      span {
        color: var(--white-color);

      }

      &::before {
        background-color: var(--blue-color);
        transform: translateX(0);
      }
    }

    &:active {
      span {
        color: var(--white-color);

      }

      &::before {
        background-color: var(--grey-color);
      }
    }
  }

  &__phone {
    display: none;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    width: 40px;
    height: 40px;
    padding: 8px;
    background-color: var(--blue-color);

    svg {
      fill: var(--white-color);
    }
  }

  &--fixed {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    z-index: 1000;
    padding: 10px 0;
    width: 100%;
  }
}

.header.hidden {
  opacity: 0;
  pointer-events: none;
}

.mobile__row {
  display: flex;
  align-items: center;
  gap: 20px;
}

.dropdown {
  position: relative;
  display: flex;
  align-items: center;
  gap: 5px;

  &__list {
    position: absolute;
    left: 0;
    top: 100%;
    display: none;
    flex-direction: column;
    gap: 5px;
    width: 200px;
    padding: 25px 10px;
    border-radius: 20px;
    background-color: var(--white-color);
    box-shadow: 0px 7px 7px -5px rgba(0, 0, 0, 0.04), 0px 10px 15px -5px rgba(0, 0, 0, 0.05), 0px 1px 3px 0px rgba(0, 0, 0, 0.05);
    transition: .2s ease-in;

    &-item {
      padding: 10px;
      transition: background-color .2s ease-in;

      @include hover {
        border-radius: 20px;
        background-color: var(--light-grey-color);
      }
    }

    &-link {
      color: var(--grey-color);
      line-height: 140%;
      transition: .2s ease-in;

      &:focus {
        outline: none;
      }

      @include hover {
        color: var(--blue-color);
      }

      &:active {
        color: var(--pink-color);
      }
    }
  }

  svg {
    width: 20px;
    height: 20px;
    stroke-width: 2px;
    stroke: #2D2F32;
    fill: transparent;
    transition: .2s ease-in;
  }

  &:focus {
    outline: none;
  }

  @include hover {
    .dropdown__list {
      display: flex;
    }

    svg {
      stroke: var(--pink-color);
      transform: rotate(180deg);
    }
  }
}

.header__link.focus-visible,
.header__logo.focus-visible,
.header__phone.focus-visible,
.header__burger.focus-visible,
.dropdown__list-link.focus-visible {
  outline: 1px solid rgb(246, 3, 3);
  outline-offset: 3px;
}


.burger {
  position: relative;
  z-index: 1000;
  display: none;
  padding: 0;
  width: 40px;
  height: 40px;
  border-radius: 5px;
  padding: 10px;
  background-color: var(--blue-color);
  cursor: pointer;

  &__line {
    position: absolute;
    right: 10px;
    top: 50%;
    width: 15px;
    height: 2px;
    border-radius: 10px;
    background-color: var(--white-color);
    transform: translateY(-50%);
    transition: opacity .2s ease-in-out;
  }

  &::before {
    content: "";
    position: absolute;
    right: 10px;
    top: 12px;
    border-radius: 10px;
    width: 18px;
    height: 2px;
    background-color: var(--white-color);
    transition: transform .2s ease-in-out, top .2s ease-in-out;
  }

  &::after {
    content: "";
    position: absolute;
    right: 10px;
    bottom: 12px;
    border-radius: 10px;
    width: 10px;
    height: 2px;
    background-color: var(--white-color);
    transition: transform .2s ease-in-out, bottom .2s ease-in-out;
  }

  &--active {
    .burger__line {
      opacity: 0;
    }

    &::before {
      top: 50%;
      width: 20px;
      background-color: #fff;
      transform: rotate(45deg);
    }

    &::after {
      top: 50%;
      bottom: auto;
      width: 20px;
      background-color: #fff;
      transform: rotate(-45deg);
    }
  }
}


@media screen and (max-width:1024px) {
  .nav {
    position: fixed;
    right: 0%;
    top: 0;
    z-index: 100;
    flex-direction: column;
    justify-content: space-between;
    overflow: auto;
    padding: 100px 30px;
    width: 50vw;
    height: 100vh;
    background-color: var(--light-grey-color);
    visibility: hidden;
    transform: translateX(100%);
    transition: transform .2s ease-in-out, visibility .2s ease-in-out;

    &--visible {
      visibility: visible;
      transform: translateX(0);
    }
  }

  .header {
    padding: 10px 0;

    &__body {
      display: flex;
      justify-content: space-between;
    }

    &__list {
      width: 100%;
      align-items: flex-start;
      flex-direction: column;
    }

    &__btn {
      width: 100%;
    }

    &__phone {
      display: flex;
    }
  }

  .dropdown {
    flex-direction: column;
    align-items: flex-start;

    &__list {
      position: static;
      display: flex;
      padding: 0 0 0 20px;
      width: 100%;
      background-color: transparent;
      box-shadow: none;

      &-item {
        padding: 0;
      }
    }

    svg {
      display: none;
    }
  }

  .burger {
    display: block;
  }
}

@media screen and (max-width:767px) {
  .nav {
    width: 80vw;
  }
}

@media screen and (max-width:500px) {
  .header {
    &__logo {
      width: 50px;
      height: 50px;
    }
  }
  .nav {
    width: 100vw;
  }
}
