.area {
  position: relative;

  &__container {
    gap: 50px;
  }

  &__subtitle {
    margin-bottom: 15px;
    font-size: clamp(1rem, 0.926rem + 0.37vw, 1.25rem);
    font-weight: var(--semibold-font);
    color: var(--black-color);
    line-height: 110%;
  }

  &__info {
    max-width: 400px;
    flex-grow: 0;
  }

  &__list {
    grid-template-columns: 1fr 1fr;
    gap: 15px;

    &:not(:last-child) {
      margin-bottom: 30px;
    }
  }

  &__item {
    cursor: pointer;
    align-items: center;
    justify-content: center;
    gap: 5px;
    border: 1px solid transparent;
    border-radius: 10px;
    width: 200px;
    padding: 15px;
    background-color: var(--white-color);
    transition: .2s ease-in;

    &-icon {
      width: 24px;
      height: 24px;
      fill: var(--pink-color);
      transition: .2s ease-in;
    }

    &-text {
      font-size: clamp(1rem, 0.964rem + 0.18vw, 1.125rem);
      font-weight: var(--semibold-font);
      line-height: 144%;
      color: var(--black-color);
      transition: .2s ease-in;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      border-color: var(--pink-color);

      &-text {
        color: var(--pink-color);
      }
    }

    &:active {
      border-color: var(--blue-color);

      &-text {
        color: var(--blue-color);
      }

      &-icon {
        fill: var(--blue-color);
      }
    }
  }

  &__map {
    width: 849px;
    max-width: 100%;
    height: 600px;
  }

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    background-image: url("../img/area/area-bg.png");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    opacity: .5;
  }
}

#map {
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 30px;
}

.leaflet-control-container {
  display: none;
}


@media screen and (max-width:900px) {
  .area {
    &__container {
      flex-direction: column;
    }

    &__info {
      max-width: 100%;
    }

    &__list {
      grid-template-columns: 1fr 1fr 1fr;
    }
  }
}

@media screen and (max-width:767px) {
  .area {
    &__list {
      grid-template-columns: 1fr 1fr;
    }

    &__map {
      height: 450px;
    }
  }
}

@media screen and (max-width:500px) {
  .area {
    &__list {
      grid-template-columns: 1fr;
    }

    &__subtitle {
      text-align: center;
    }

    &__item {
      justify-content: center;
      width: 100%;
    }
  }
}
