.features {
  margin-top: -300px;
  position: relative;
  z-index: 1;
  padding: 203px 0 60px;

  &__body {
    position: relative;
    max-width: 1330px;
    margin: auto;
    border-radius: 30px;
    padding: 50px;
    background-color: var(--blue-color);
  }

  &__row {
    gap: 50px;
  }

  &__list {
    grid-template-columns: 1fr 1fr;
    gap: 15px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 10px;
    padding: 30px;
    width: 280px;
    background-color: var(--white-color);

    &-icon {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-bottom: 25px;
      padding: 25px;
      border-radius: 100%;
      width: 120px;
      height: 120px;
      background-color: rgba(73, 41, 255, 0.10);

      svg {
        display: block;
        fill: var(--blue-color);
      }
    }

    &-title {
      margin-bottom: 10px;
      font-size: clamp(1.563rem, 1.384rem + 0.89vw, 2.188rem);
      font-weight: var(--semibold-font);
      line-height: 114%;
    }

    &-text {
      font-weight: var(--semibold-font);
      line-height: 150%;
      color: var(--grey-color);
      text-align: center;
    }
  }

  &__title {
    margin-bottom: 30px;
    font-size: clamp(1.375rem, 0.946rem + 2.14vw, 2.875rem);
    font-weight: var(--semibold-font);
    line-height: 110%;
    color: var(--white-color);
  }

  &__text {
    margin-bottom: 40px;
    font-size: clamp(1rem, 0.964rem + 0.18vw, 1.125rem);
    color: var(--light-grey-color);
    line-height: 144%;
  }

  &__sublist {
    flex-direction: column;
    gap: 15px;
    margin-bottom: 40px;

    &-item {
      position: relative;
      padding-left: 40px;
      font-size: clamp(1rem, 0.964rem + 0.18vw, 1.125rem);
      font-weight: var(--semibold-font);
      line-height: 144%;
      text-transform: uppercase;
      color: var(--light-grey-color);

      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 30px;
        height: 30px;
        background-image: url("../img/features/list-arrow.png");
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
      }
    }
  }

  &__btn {
    &:active {
      span {
        color: var(--blue-color);

      }

      &::before {
        background-color: var(--grey-color);
      }
    }
  }
}

.star-1 {
  position: absolute;
  right: 115px;
  bottom: 130px;
  z-index: 1;
  width: 27px;
  height: 27px;
  transform: rotate(0.291deg);
  animation: twinkle 2s infinite;
  animation-timing-function: cubic-bezier(0.5, 0, 0.5, 1);

  svg {
    display: block;
    width: 100%;
    height: 100%;
    fill: var(--white-color);
    opacity: 0.2;
  }
}

.star-2 {
  position: absolute;
  right: 154px;
  bottom: 57px;
  z-index: 1;
  width: 43px;
  height: 43px;
  transform: rotate(15.086deg);
  animation: twinkle 2s infinite;
  animation-timing-function: cubic-bezier(0.5, 0, 0.5, 1);

  svg {
    display: block;
    width: 100%;
    height: 100%;
    fill: var(--white-color);
    opacity: 0.2;
  }
}

.star-3 {
  position: absolute;
  right: 32px;
  bottom: 43px;
  z-index: 1;
  width: 90px;
  height: 90px;
  transform: rotate(-9.8deg);
  animation: twinkle 2s infinite;
  animation-timing-function: cubic-bezier(0.5, 0, 0.5, 1);

  svg {
    display: block;
    width: 100%;
    height: 100%;
    fill: var(--white-color);
    opacity: 0.2;
  }
}

@keyframes twinkle {

  0%,
  100% {
    transform: scale(0);
    opacity: 0;
  }

  50% {
    transform: scale(1);
    opacity: 1;
  }
}

@media screen and (max-width:1024px) {
  .features {
    &__row {
      flex-direction: column;
    }

    &__body {
      border-radius: 0;
    }

    &__item {
      width: auto;
    }
  }
}

@media screen and (max-width:600px) {
  .features {
    &__item {
      padding: 30px 10px;

      &-icon {
        padding: 15px;
        width: 80px;
        height: 80px;
      }
    }

    &__body {
      padding: 30px 15px;
    }
  }
}

@media screen and (max-width:500px) {
  .features {
    padding: 203px 0 0;
  }
}
