.about {
  &__title {
    margin-bottom: 38px;
    font-size: 16px;
    font-weight: var(--regular-font);
    line-height: 150%;
    color: var(--grey-color);
  }

  &__slider {
    padding: 40px 0;

    .swiper-button-disabled {
      opacity: 0 !important;
    }

    .swiper-slide-active {

      .about__images,
      .about__info {
        transform: scale(1);
      }
    }
  }

  &__slide {
    position: relative;
    align-items: center;
    justify-content: center;
    gap: 100px;
    transform: scale(0);
    transition: .2s ease-in;
  }

  &__info {
    max-width: 500px;
    transform: scale(0);
    transition: .2s ease-in;
  }

  &__subtitle {
    gap: 20px;
    margin-bottom: 20px;
    font-size: clamp(1.25rem, 1.071rem + 0.89vw, 1.875rem);
    font-weight: var(--semibold-font);
    line-height: 114%;
    color: var(--pink-color);

    span {
      color: var(--black-color);
    }
  }

  &__text {
    line-height: 150%;
    color: var(--grey-color);
  }

  &__images {
    position: relative;
    width: 544px;
    height: 620px;
    transform: scale(0);
    transition: 1s ease-in-out;
  }

  &__image {
    position: absolute;
    left: 0;
    top: 0;
    border: 5px solid #FFF3F8;
    border-radius: 30px;
    width: 100%;
    height: 600px;
    object-fit: cover;
    transition: .2s ease-in;

    &--back {
      z-index: -1;
      top: -20px;
      left: -5px;
      transform: rotate(3.557deg);
    }
  }

  &__nav {
    &-btn {
      position: absolute;
      top: 50%;
      z-index: 10;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 100%;
      width: 60px;
      height: 60px;
      transform: translateY(-50%);
      background-color: var(--white-color);
      transition: .2s ease-in;

      svg {
        width: 30px;
        height: 30px;
        stroke: var(--black-color);
        transition: .2s ease-in;
      }

      &--prev {
        left: -30px;
      }

      &--next {
        right: -30px;

        svg {
          transform: rotate(180deg);
        }
      }

      &:focus {
        outline: none;
      }

      @include hover {
        background-color: var(--pink-color);

        svg {
          stroke: var(--white-color);
        }
      }

      &:active {
        background-color: var(--blue-color);

        svg {
          stroke: var(--white-color);
        }
      }
    }
  }
}

@media screen and (max-width:1024px) {
  .about {
    &__slider {
      padding:40px 0 0 0;
    }
    &__slide {
      flex-direction: column;
      gap: 50px;
    }
    &__info {
      max-width: 100%;
    }
  }
}

@media screen and (max-width:600px) {
  .about {
    &__images {
      width: 90%;
      height: 500px;
    }
    &__image {
      height: 500px;
    }
  }
}

@media screen and (max-width:600px) {
  .about {
    &__images {
      height: 400px;
    }
    &__image {
      height: 400px;
    }
  }
}
