:root {
  // base
  --font-family: "Inter", sans-serif;
  --font-hyundai: "Hyundai Sans", sans-serif;
  --font-regular: 400;
  --semibold-font: 600;
  --bold-font: 700;
  --content-width: 1300px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --white-color: #ffffff;
  --black-color: #2D2F32;
  --pink-color: #EA085D;
  --blue-color: #1E0F3A;
  --grey-color: #8D949F;
  --light-grey-color: #FAFAFA;
}
